@import "../../../sass/global.scss";
.skills-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
 align-items: center;
  overflow: hidden;
  h1 {
    font-size: 90px;
  }
  ul {
    display: flex;
    margin: 10px;
    list-style: none;
    padding: 0;
  }
  .items-container {
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    object-fit: cover;

    .item {
      width: 220px;
      height: 150px;
      border-radius: 20px;
      border: 2px solid aliceblue;
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: aliceblue;
      position: relative;
      cursor: pointer;
      transition: all 0.5s ease;

      h3 {
        position: absolute;
        font-size: 20px;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
        border-radius: 15px;
      }
      &:hover {
        background: $mainColor;
        img {
          opacity: 0.4;
          z-index: 0;
        }
      }
    }
  }
}
